import React, { Component } from 'react';
import { Layout } from 'antd';
import moment from 'moment';
import Wrapper from '../components/Wrapper';
import HomeworkForm from '../containers/HomeworkForm';
import TableItems from '../containers/TableItems';
import request from '../utilities/request';
import api from '../config/api';

const { Content } = Layout;

class Home extends Component {
  state = {
    isLoading: true,
    data: []
  };

  componentDidMount() {
    this.getHomework();
  }

  addHomework = async () => {

  }

  getHomework = async () => {
    const { statusCode, data } = await request.get(api.url.homework)
    if (statusCode) {
      const convertedData = data.map((item, key) => {
        const {
          subject,
          description,
          type,
          due_date,
          due_time,
          appointment
        } = item;

        moment.locale('th');

        return {
          key,
          subject,
          description,
          type,
          date: moment(due_date).format('LL'),
          time: moment(due_time).format('LT'),
          appointment,
        };
      });
      this.setState({
        isLoading: false,
        data: convertedData
      });
    }
  }

  updateHomework = async () => {

  }

  removeHomework = async () => {

  }

  render() {
    const { data, isLoading } = this.state;
    return (
      <Wrapper>
        <Content style={{ margin: '24px 16px', padding: 24, background: '#fff', minHeight: 280 }}>
          <Layout style={{ background: '#fff' }}>
            <HomeworkForm
              onSubmit={addHomework}
            />
            <TableItems
              isLoading={isLoading}
              data={data}
            />
          </Layout>
        </Content>
      </Wrapper>
    );
  }
}

export default Home;


